//
// Scaffolding
// --------------------------------------------------
body {
  color: @text-color;
  background-color: @body-bg;
  padding-top: 60px;
}

// Links
a {
  color: @link-color;
  text-decoration: none;

  &:hover,
  &:focus {
    color: @link-hover-color;
    text-decoration: @link-hover-decoration;
  }

  &:focus {
    .tab-focus();
  }
}

.nav 
  > li 
    > a {
      &:hover,
      &:focus {
        background-color: @nav-li-a-hover-focus;
        color: #fff;
      }
    }

//
// Navbar
// --------------------------------------------------

// Default navbar
.navbar-default {
  background-color: @navbar-default-bg;
  border-color: @navbar-default-border;
  
  @shadow: inset 0 1px 0 rgba(255,255,255,.15), 0 1px 5px rgba(0,0,0,.075);
  .box-shadow(@shadow);
  .navbar-text {
    color: @navbar-default-color;
  }

  .navbar-brand {
    color: @navbar-default-brand-color;
    margin-top: -5px;
    &:hover,
    &:focus {
      color: @navbar-default-brand-hover-color;
      //background-color: @navbar-default-brand-hover-bg;
    }
  }

  .navbar-nav {
    > li > a {
      color: @navbar-default-link-color;

      &:hover,
      &:focus {
        color: @navbar-default-link-hover-color;
        background-color: darken(@navbar-default-bg, 5%);
        //background-color: @navbar-default-link-hover-bg;
      }
    }
    > .active > a {
      &,
      &:hover,
      &:focus {
        color: @navbar-default-link-active-color;
        //background-color: @navbar-default-link-active-bg;
      }
    }
    > .disabled > a {
      &,
      &:hover,
      &:focus {
        color: @navbar-default-link-disabled-color;
        //background-color: @navbar-default-link-disabled-bg;
      }
    }
  }

  .navbar-nav > .open > a,
  .navbar-nav > .active > a {
    #gradient > .vertical(@start-color: darken(@navbar-default-bg, 5%); @end-color: darken(@navbar-default-bg, 2%));
    //.box-shadow(inset 0 3px 9px rgba(0,0,0,.075));
  }
  
  // Dropdown menu items
  .navbar-nav {
    // Remove background color from open dropdown
    > .open > a {
      &,
      &:hover,
      &:focus {
        color: @navbar-default-link-active-color;
      }
    }
  }
}

// Undo rounded corners in static and fixed navbars
.navbar-static-top,
.navbar-fixed-top,
.navbar-fixed-bottom {
  border-radius: 0;
}

/* Hide for mobile, show later */
.sidebar {
  display: none;
}
.container-fluid{
  margin-left: 0px;
}
@media (min-width: 768px) {
  .sidebar {
    position: fixed;
    top: 45px;
    bottom: 0;
    left: 0;
    z-index: 1000;
    display: block;
    padding: 20px;
    overflow-x: hidden;
    overflow-y: auto; /* Scrollable contents if viewport is shorter than content. */
    //background-color: #cdd2db;//#DFE2E7;//#f5f5f5; //NOVO
    background: none repeat scroll 0% 0% @sidebar-media-background;
    //border-right: 1px solid #eee;
    width: 185px;
  }
  
  .sidebar-recolher {
    position: fixed;
    bottom: 0;
    left: 145px;
    z-index: 1000;
    display: block;
    padding: 0px;
    overflow-x: hidden;
    overflow-y: auto; /* Scrollable contents if viewport is shorter than content. */
    //background-color: #cdd2db;//#DFE2E7;//#f5f5f5; //NOVO
    background: none repeat scroll 0% 0% @sidebar-media-background;
    //border-right: 1px solid #eee;
    width: 40px;
  }
  
  .sidebar-active > .sidebar-recolher{
    left:0;
  }
  
  .sidebar-recolher > li > a {
    padding-left: 13px;
  }
  
  .container-fluid{
    margin-left: 185px;
  }
  
  .container-active{
    margin-left: 40px;
  }
  
  .container-menu-oculto {
    margin-left: 0px;
  }
  
  .sidebar-active{
    width: 40px;
  }
  
  .sidebar-active > .nav-sidebar > li > a {
    padding: 7px 18px 7px 10px;
  }
  
  .sidebar-active > .nav-sidebar > li > a .caret{
    right: 6px;
  }
  
  .sidebar-active > .nav-sidebar > li > a .link_menu,
  .sidebar-active > .nav-sidebar > li > ul > li > a .link_menu{
    /*color: orange;*/
    display: none;
  }
  
  .sidebar-active > .nav-sidebar > li > ul > li > a {
    padding: 5px 14px;
  }
}

