// Default navbar
.navbar-default {
  .navbar-toggle {
    border-color: @navbar-default-toggle-border-color;
    &:hover,
    &:focus {
      background-color: @navbar-default-toggle-hover-bg;
    }
    .icon-bar {
      background-color: @navbar-default-toggle-icon-bar-bg;
    }
  }
  
  
  // Dropdown menu items
  .navbar-nav {
    @media (max-width: @grid-float-breakpoint-max) {
      // Dropdowns get custom display when collapsed
      .open .dropdown-menu {
        > li > a {
          color: @navbar-default-link-color;
          &:hover,
          &:focus {
            color: @navbar-default-link-hover-color;
            background-color: @navbar-default-link-hover-bg;
          }
        }
      }
    }
  }
  
}


.table {
  // Cells
  > thead,
  > tbody,
  > tfoot {
    > tr {
      > th,
      > td {
        padding: 3px;
        border-top: 1px solid @table-border-color;
      }
    }
  }
  // Bottom align for column headings
  > thead > tr > th {
    border-bottom: 2px solid @table-border-color-thead;//@table-border-color;
  }
}

//Zebrado
.table-striped {
  > tbody > tr:nth-child(odd) {
    background-color: @table-bg-accent;//rgba(223, 226, 231, 0.5);//rgba(223, 226, 231, 0.7);//#dfe2e7;//@table-bg-accent;
    
  }
}

.table-hover {
  > tbody > tr:hover {
    background-color: @table-bg-hover;//#C0D4F7;//#A4B9DE;//#66CCFF;//@table-bg-hover;
  }
}

