.sf-popover.top {
  margin-top: -10px;
}
.sf-popover.right {
  margin-left: 10px;
}
.sf-popover.bottom {
  margin-top: 10px;
}
.sf-popover.left {
  margin-left: -10px;
}
.sf-popover > .arrow,
.sf-popover > .arrow:after {
  position: absolute;
  display: block;
  width: 0;
  height: 0;
  border-color: transparent;
  border-style: solid;
}
.sf-popover > .arrow {
  border-width: 11px;
}
.sf-popover > .arrow:after {
  content: "";
  border-width: 10px;
}
.sf-popover.top > .arrow {
  bottom: -11px;
  left: 50%;
  margin-left: -11px;
  border-top-color: @sf-popover-arrow-border-color;
  border-bottom-width: 0;
}
.sf-popover.top > .arrow:after {
  bottom: 1px;
  margin-left: -10px;
  content: " ";
  border-top-color: @sf-popover-arrow-border-color;
  border-bottom-width: 0;
}
.sf-popover.right > .arrow {
  top: 50%;
  left: -11px;
  margin-top: -11px;
  border-right-color: @sf-popover-arrow-border-color;
  border-left-width: 0;
}
.sf-popover.right > .arrow:after {
  bottom: -10px;
  left: 1px;
  content: " ";
  border-right-color: @sf-popover-arrow-border-color;
  border-left-width: 0;
}
.sf-popover.bottom > .arrow {
  top: -11px;
  left: 50%;
  margin-left: -11px;
  border-top-width: 0;
  border-bottom-color: @sf-popover-arrow-border-color;
}
.sf-popover.bottom > .arrow:after {
  top: 1px;
  margin-left: -10px;
  content: " ";
  border-top-width: 0;
  border-bottom-color: @sf-popover-arrow-border-color;
}
.sf-popover.left > .arrow {
  top: 50%;
  right: -11px;
  margin-top: -11px;
  border-right-width: 0;
  border-left-color: @sf-popover-arrow-border-color;
}
.sf-popover.left > .arrow:after {
  right: 1px;
  bottom: -10px;
  content: " ";
  border-right-width: 0;
  border-left-color: @sf-popover-arrow-border-color;
}